.breadcrumbs{
  display: inline-block;
  margin:0;
  padding: 0;

  .breadcrumbs__item{
    list-style: none;
    display: inline-block;
    &:after{
      font-family: icomoon;
      content: " \3e ";
    }

    &.breadcrumbs__item--active,
    &.breadcrumbs__item:last-child{
      &:after{
        content: '';
      }
    }
  }
  .breadcrumbs__link{
    color: $color-grey-dark;
    text-decoration: none;
  }
}