@font-face {
  font-family: 'icomoon';
  src:    url('../fonts/icomoon.eot?qw9ph3');
  src:    url('../fonts/icomoon.eot?qw9ph3#iefix') format('embedded-opentype'),
  url('../fonts/icomoon.woff2?qw9ph3') format('woff2'),
  url('../fonts/icomoon.ttf?qw9ph3') format('truetype'),
  url('../fonts/icomoon.woff?qw9ph3') format('woff'),
  url('../fonts/icomoon.svg?qw9ph3#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-close:before {
  content: "\e900";
}
.icon-breadcrumb-arrow:before {
